







import Vue from 'vue';
import { mapGetters } from 'vuex';

export default Vue.extend({
  name: 'ImageContainer',

  props: {
    src: {
      type: String,
      required: true,
    },

    generalAlt: {
      type: String,
      default: 'Image',
    },
  },

  computed: {
    ...mapGetters('favorites', {
      favorites: 'favorites',
    }),

    isFavorite(): boolean {
      return this.favorites.includes(this.src);
    },
  },

  methods: {
    clickHandler(src: string) {
      if (this.isFavorite) {
        this.$store.dispatch('favorites/removeFromFavorites', src);
      } else {
        this.$store.dispatch('favorites/addToFavorite', src);
      }
    },
  },
});
