






import Vue from 'vue';
import { mapGetters } from 'vuex';

export default Vue.extend({
  name: 'DogsPicker',

  data() {
    return {
      selectedBreed: 'random',
    };
  },

  computed: {
    ...mapGetters('breeds', {
      breeds: 'dogsBreeds',
    }),
  },

  watch: {
    async selectedBreed(value) {
      if (value) {
        this.$emit('input', value);
      }
    },
  },
});
